/**
 * reducer
 * @param initState state
 */
export function createReducer(initState, reducerMap){
    return (state = initState, action = {}) => {
        const reducer = reducerMap[action.type];
        // console.log("action",action);
        return reducer
            ? reducer(state, action)
            : state;
    }
}

